.BetSlider {
    width: 15em;
    height: 2em;
    background-color: #72727294;
    user-select: none;
    border: 0.1em solid #1a181870;
    cursor: pointer;
}

.BetSlider-Filled {
    height: 100%;
    background-color: #c6c4c4;
}

.BetSlider-Value {
    position: relative;
    float: left;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: bold;
    color: #f2f2f2;
    -webkit-text-stroke: 0.5px gray;
    margin-top: -0.1em;
}