.TopTools {
    position: absolute;
    top: 0;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    padding-top: 0.25em;
}

.TopTools-Right {
    display: inline-block;
    height: 100%;
    width: 25%;
    box-sizing: border-box;
    text-align: right;
    word-break: break-all;
    white-space: normal;
    padding-right: 0.2em;
    vertical-align: top;
}

.TopTools-Center {
    display: inline-block;
    width: 50%;
    box-sizing: border-box;
    text-align: center;
    vertical-align: top;
}

.TopTools-Left {
    display: inline-block;
    height: 100%;
    width: 25%;
    box-sizing: border-box;
    text-align: left;
    vertical-align: top;
}

.TopTools-IconButton {
    display: inline-block;
    width: 1.5em;
    height: 1.5em;
    cursor: pointer;
    vertical-align: top;
}

.TopTools-Icon {
    width: 100% !important;
    height: 100% !important;
}

.TopTools-PlayerName {
    display: inline-block;
    vertical-align: top;
    font-weight: bold;
    margin-left: 1em;
    cursor: pointer;
    color: #c19331;
    -webkit-text-stroke: 1px #00000088;
    padding-right: 0.2em;
    text-align: center;
}

.TopTools-PlayerStats {
    padding-top: 2em;
}

.TopTools-PlayerStats-Money {
    vertical-align: top;
    font-size: 1.25em;
    -webkit-text-stroke: 0.5px #272727;
    color: #90c990;
    font-weight: bold;
}

.TopTools-PlayerStats-Place {
    font-size: 0.9em;
    color: #3fde3f;
    -webkit-text-stroke: 0.5px #29422499;
}

.TopTools-HighScoresButton {
    display: inline-block;
    vertical-align: top;
    margin-left: 1em;
    color: #000000;
    -webkit-text-stroke: 1px #9c7e3fbf;
    font-weight: bold;
    cursor: pointer;
}
