.Bet {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.Bet-Item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 0.5em;
    min-width: 6em;
    height: 4.5em;
    margin-bottom: 0.75em;
}

@media screen and (max-width: 560px) {
	.Bet-Item {
		min-width: 3.75em;
	}
}

@media screen and (max-width: 640px) and (min-width: 561px) {
	.Bet-Item {
		min-width: 4.5em;
	}
}

.Bet-Item-Images-Box {
    width: 2.7em;
    height: 2.7em;
}

.Bet-Item-Image {
    width: 100%;
    border: 0.1em solid #2d2828;
    cursor: pointer;
    margin: 0.1em;
}

.Bet-Item-Scrap-Image {
    display: block;
    width: 2.4em;
    margin: auto;
    border: none;
    z-index: 10;
    cursor: pointer;
    margin-top: -3.1em;
    margin-left: 0.3em;
}

.Bet-Item-Image-Selected {
    border: 0.2em solid yellow;
    margin: 0;
}

.Bet-Item-Count {
    color: #9affb4;
    -webkit-text-stroke: 0.5px #000000c9;
    font-size: 1.1em;
    min-width: 1px;
    height: 1em;
    margin-top: 0.2em;
    cursor: pointer;
}