.Modal {
    width: 20em;
    background-color: #a3a3a3c9;
    outline: none;
    border-radius: 3px;
    padding: 0.7em;
}

.Overlay {
    position: fixed;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    inset: 0px;    
}